<template>
  <div>
  </div>
</template>

<script>
import * as d3 from 'd3'
import latent_features_waarden from '@/data/latent_features_waarden.json'
import latent_features_waarden_huidige_sector from '@/data/latent_features_waarden_huidige_sector.json'
import latent_features_waarden_functietype from '@/data/latent_features_waarden_functietype.json'

export default {
  name:"BubblePlotWaarde",
  props:["selected", "significant_sectors", "significant_functietypes"],
  data: function(){
    return{
      latent_features_waarden:latent_features_waarden,
      latent_features_huidige_sector: latent_features_waarden_huidige_sector,
      latent_features_functietype: latent_features_waarden_functietype,
      // Data for latent features bubble plot
      bubble_latent_features_waarden_data:null,
      bubble_latent_features_waarden_options:{},
    }
  },
  mounted(){
    
    // Prepare data for latent features plot
    var temp_array_data = []
    for(var i=0; i<this.latent_features_waarden.length; i++){
      temp_array_data.push({
        'waarde': this.latent_features_waarden[i]['waarde'],
        'x':this.latent_features_waarden[i]['0'],
        'y':this.latent_features_waarden[i]['1'],
        'theta':this.latent_features_waarden[i]['theta'],
        'bias':this.latent_features_waarden[i]['bias'],
      })
    }
    this.bubble_latent_features_waarden_data = temp_array_data;    

    // Find min and max for linear scale
    var min_linear_scale = this.bubble_latent_features_waarden_data.reduce((minimum, entry) => Math.min(minimum, entry.x, entry.y), 99)
    var max_linear_scale = this.bubble_latent_features_waarden_data.reduce((minimum, entry) => Math.max(minimum, entry.x, entry.y), -99)
    var min_max_linear_scale = Math.max(-min_linear_scale, max_linear_scale)

    // set the dimensions and margins of the graph
    var container_width = document.getElementById("bubble-chart-latent-features-waarden").offsetWidth
    var svg_width = Math.min(window.innerHeight-200, container_width)
    svg_width = Math.max(300, svg_width)
    var margin = {top: 5, right: 5, bottom: 5, left: 5},
        width = svg_width - margin.left - margin.right,
        height = svg_width - margin.top - margin.bottom; 

    // append the svg object to the body of the page
    var svg = d3.select("#bubble-chart-latent-features-waarden")
      .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
      .append("g")
        .attr("transform",
              "translate(" + margin.left + "," + margin.top + ")");
    
    // Define linear scale
    var linear_scale = d3.scaleLinear()
      .domain([-min_max_linear_scale, min_max_linear_scale])
      .range([ 0, width ]);
    // svg.append("g")
    //   .attr("transform", "translate(0," + height + ")")
    //   .call(d3.axisBottom(x)); 
      
    // Color function
    var myColor = d3.scaleSequential().domain([0,6.28])
                    .interpolator(d3.interpolateRainbow);  
    
    // Add dots
    svg.selectAll("dot")
      .data(this.bubble_latent_features_waarden_data)
      .enter()
      .append("circle")
        .attr("cx", function (d) { return linear_scale(d.x); } )
        .attr("cy", function (d) { return linear_scale(d.y); } )
        .attr("r", function(d) { return d.bias*5})
        .attr("fill", function(d){return myColor(d.theta) }) 

    // Add text labels
    svg.selectAll("text")
      .data(this.bubble_latent_features_waarden_data)
      .enter()
      .append("text")
        .attr("x", function (d) { return linear_scale(d.x); } )
        .attr("y", function (d) { return linear_scale(d.y); } )
        .text(function(d){ return d.waarde})
        .attr("font-size", "10px")

    // Add X axis
    svg
      .append("g")
      .attr("transform", "translate(0," + height/2 + ")")
      .call(d3.axisBottom(linear_scale).tickValues([]))
      ;
    
    // Add Y axis
    svg
      .append("g")
      .attr("transform", "translate(" + width/2 + ",0)")
      .call(d3.axisLeft(linear_scale).tickValues([]));  

    // Initialize variables
    var temp_dot_to_plot //the selected sector or functietype;
    var temp_dots_to_plot //the significant sectors or functietypes;
    var temp_lines_to_plot //the connection between selected and significants;

    // If sector is selected:    
    if(this.selected.sector_or_functietype == 'sector' ){

      // Plot a dot with the huidige sector
      temp_dot_to_plot = this.latent_features_huidige_sector.filter(
                        entry => entry["HUIDIGE SECTOR"]==this.selected.sector)
      svg.selectAll("dot_sector")
          .data(temp_dot_to_plot)
          .enter()
          .append("circle")
          .attr("cx", function (d) { return linear_scale(d.x); } )
          .attr("cy", function (d) { return linear_scale(d.y); } )
          .attr("r", "8")
          .attr("fill", "black") 
      svg.selectAll("text_sector")
          .data(temp_dot_to_plot)
          .enter()
          .append("text")
          .attr("x", function (d) { return linear_scale(d.x) + 10; } )
          .attr("y", function (d) { return linear_scale(d.y) + 5; } )
          .text(function(d){ return d["HUIDIGE SECTOR"]})
          .attr("font-size", "12px")  
          .attr("font-weight", "bold")

      // Plot significant functie types
      temp_dots_to_plot = this.latent_features_functietype.filter(
                        entry => this.significant_functietypes.includes(entry["HUIDIG FUNCTIETYPE"]))      
      svg.selectAll("dot_significant_functietypes")
          .data(temp_dots_to_plot)
          .enter()
          .append("circle")
          .attr("cx", function (d) { return linear_scale(d.x); } )
          .attr("cy", function (d) { return linear_scale(d.y); } )
          .attr("r", "6")
          .attr("fill", "black") 
      svg.selectAll("text_significant_functietypes")
          .data(temp_dots_to_plot)
          .enter()
          .append("text")
          .attr("x", function (d) { return linear_scale(d.x) + 10; } )
          .attr("y", function (d) { return linear_scale(d.y) + 5; } )
          .text(function(d){ return d["HUIDIG FUNCTIETYPE"]})
          .attr("font-size", "10px")  
          .attr("font-weight", "bold")

      // Plot lines between the selected sector and the significant functietypes
      temp_lines_to_plot = []
      for(i=0; i<temp_dots_to_plot.length; i++){
        temp_lines_to_plot.push({
          x1:temp_dot_to_plot[0].x,
          x2:temp_dots_to_plot[i].x,
          y1:temp_dot_to_plot[0].y,
          y2:temp_dots_to_plot[i].y,
        })
      }
      svg.selectAll("lines_between_sector_and_functietypes")
        .data(temp_lines_to_plot)
        .enter()
        .append("line")
        .attr("x1", function (d) { return linear_scale(d.x1); } )
        .attr("y1", function (d) { return linear_scale(d.y1); })
        .attr("x2", function (d) { return linear_scale(d.x2); })
        .attr("y2", function (d) { return linear_scale(d.y2); })   
        .attr("stroke", "black")   
        .style("stroke-dasharray", ("3, 3"))
    
    // If functie type selected, plot this functie type and the corresponding sectors
    }else if(this.selected.sector_or_functietype == 'functietype' ){
      
      // Plot a dot with the selected functietype
      temp_dot_to_plot = this.latent_features_functietype.filter(
                        entry => entry["HUIDIG FUNCTIETYPE"]==this.selected.functietype)
      svg.selectAll("dot_sector")
          .data(temp_dot_to_plot)
          .enter()
          .append("circle")
          .attr("cx", function (d) { return linear_scale(d.x); } )
          .attr("cy", function (d) { return linear_scale(d.y); } )
          .attr("r", "8")
          .attr("fill", "black") 
      svg.selectAll("text_sector")
          .data(temp_dot_to_plot)
          .enter()
          .append("text")
          .attr("x", function (d) { return linear_scale(d.x) + 10; } )
          .attr("y", function (d) { return linear_scale(d.y) + 5; } )
          .text(function(d){ return d["HUIDIG FUNCTIETYPE"]})
          .attr("font-size", "12px")  
          .attr("font-weight", "bold")

      // Plot significant sectors
      temp_dots_to_plot = this.latent_features_huidige_sector.filter(
                        entry => this.significant_sectors.includes(entry["HUIDIGE SECTOR"]))      
      svg.selectAll("dot_significant_sectors")
          .data(temp_dots_to_plot)
          .enter()
          .append("circle")
          .attr("cx", function (d) { return linear_scale(d.x); } )
          .attr("cy", function (d) { return linear_scale(d.y); } )
          .attr("r", "6")
          .attr("fill", "black") 
      svg.selectAll("text_significant_sectors")
          .data(temp_dots_to_plot)
          .enter()
          .append("text")
          .attr("x", function (d) { return linear_scale(d.x) + 10; } )
          .attr("y", function (d) { return linear_scale(d.y) + 5; } )
          .text(function(d){ return d["HUIDIGE SECTOR"]})
          .attr("font-size", "10px")  
          .attr("font-weight", "bold")

      // Plot lines between the selected sector and the significant functietypes
      temp_lines_to_plot = []
      for(i=0; i<temp_dots_to_plot.length; i++){
        temp_lines_to_plot.push({
          x1:temp_dot_to_plot[0].x,
          x2:temp_dots_to_plot[i].x,
          y1:temp_dot_to_plot[0].y,
          y2:temp_dots_to_plot[i].y,
        })
      }
      svg.selectAll("lines_between_sector_and_functietypes")
        .data(temp_lines_to_plot)
        .enter()
        .append("line")
        .attr("x1", function (d) { return linear_scale(d.x1); } )
        .attr("y1", function (d) { return linear_scale(d.y1); })
        .attr("x2", function (d) { return linear_scale(d.x2); })
        .attr("y2", function (d) { return linear_scale(d.y2); })   
        .attr("stroke", "black")   
        .style("stroke-dasharray", ("3, 3"))    
    



      
      // svg.selectAll("text_sector")
      //     .data(temp_dot_to_plot)
      //     .enter()
      //     .append("circle")
      //       .attr("cx", function (d) { return linear_scale(d.x); } )
      //       .attr("cy", function (d) { return linear_scale(d.y); } )
      //       .attr("r", "10")
      //       .attr("fill", "black")             
    }
    
  },

}
</script>

<style>
  #chart_container{
    height:500px;
  }
</style>