<template>
  <div>
    <!-- Title section -->
    <section class="section has-text-centered">
      <h1 class="title">Jobmatcher insights</h1>
      <h2 class="subtitle">Een interactieve data visualisatie van de jobmatcher antwoorden.</h2>
    </section>
    <!-- Select sector / function type -->
    <section class="section has-text-centered">
      <p>Selecteer functietype </p>
      <div class="select">
        <select v-model="selected.functietype" @change="selection('functietype')">
          <option>Fuctietype</option>
          <option>Aankoop</option>
          <option>Administratieve ondersteuning, secretariaat</option>
          <option>Back-office beheer (planning, voorraad, nazorg, ...) of onderhoud</option>
          <option>Boekhouding, financiën of audit</option>
          <option>Chauffeur, transport of logistiek planner</option>
          <option>Creatie, ontwikkeling en/of onderzoek</option>
          <option>Culturele begeleiding</option>
          <option>Directe productie</option>
          <option>Engineering en techniek</option>
          <option>Human resources-beheer</option>
          <option>ICT-expert of ICT-ondersteuning (bv. informatica en telecom-beheer)</option>
          <option>Juridisch advies</option>
          <option>Marketing, communicatie of verkoopsondersteuning</option>
          <option>Medische begeleiding en gezondheidszorg</option>
          <option>Mensgerichte begeleiding of opleiding</option>
          <option>Regelgeving en rechtspraak (overheid)</option>
          <option>Student(e)</option>
          <option>Veiligheid, controle en inspectie</option>
          <option>Verkoop en advies aan particulieren</option>
          <option>Verkoop, advies of consulting aan bedrijven</option>
          <option>Werkzoekende (niet-werkend)</option>
        </select>
      </div>
      <div>
        OF<br>selecteer een sector
      </div>
      <div class="select">
        <select v-model="selected.sector" @change="selection('sector')">
          <option>Huidige sector</option>
          <option>Bank en verzekeringen</option>
          <option>Boekhouding en audit</option>
          <option>Bouw en immobiliën</option>
          <option>Chemie- en kunststofnijverheid, milieu</option>
          <option>Cultuur, sociale begeleiding en ontspanning</option>
          <option>Drank, voedings- en landbouwnijverheid</option>
          <option>Energie, milieu, water- en afvalverwerking</option>
          <option>Groot- en kleinhandel</option>
          <option>HR-dienstverlening</option>
          <option>Horeca en toerisme</option>
          <option>Informatica- en telecom-dienstverlening</option>
          <option>Juridische beroepen</option>
          <option>Logistiek en transport</option>
          <option>Marketing, media en creatieve dienstverlening</option>
          <option>Medische, paramedische en zorgsector</option>
          <option>Metaal- en houtnijverheid</option>
          <option>Onderwijs</option>
          <option>Overheid en andere parastatalen</option>
          <option>Politie en veiligheidsdiensten</option>
          <option>Schoonheid en lichaamsverzorging</option>
          <option>Student(e)</option>
          <option>Werkzoekende (niet-werkend)</option>
        </select>
      </div>
    </section>
    <!-- Algemene informatie deelnemers -->
    <section class="section has-text-centered" v-if="selected.sector_or_functietype">
      <h3 class="title is-3">
        Over de test-afnemers
      </h3>
      <h3 class="subtitle is-5 content">
        <p v-if="selected.sector_or_functietype=='sector'">
          Er waren {{x_respondents}} test-afnemers uit de {{selected.sector}}. 
        </p>
        <p v-if="selected.sector_or_functietype=='functietype'">
          Er waren {{x_respondents}} test-afnemers met functie type {{selected.functietype}}.
        </p>
        <p>
          Met de volgende grafieken tonen we enkele algemene informatie, zoals verdeling man/vrouw, leeftijd en opleidingsniveau.
        </p>
      </h3>
      <histogram-age :chartData="histogram_age_data" 
                      :options="histogram_age_options" 
                      :css-classes="'chartcontainer histogram-age'"
                      v-if="histogram_age_data"></histogram-age>

      <horizontal-bar-counts :chartData="bar_chart_countsdegree_data" 
                              :options="bar_chart_countsdegree_options" 
                              :css-classes="'chartcontainer barchart-education'"
                              v-if="bar_chart_countsdegree_data"></horizontal-bar-counts>

      <horizontal-bar-counts :chartData="bar_chart_counts_sector_data" 
                              :options="bar_chart_counts_sector_options" 
                              :css-classes="'chartcontainer barchart-sector'"
                              v-if="selected.sector_or_functietype=='functietype'"></horizontal-bar-counts>

      <horizontal-bar-counts :chartData="bar_chart_counts_functietype_data" 
                              :options="bar_chart_counts_functietype_options"
                              :css-classes="'chartcontainer barchart-functietype'" 
                              v-if="selected.sector_or_functietype=='sector' "></horizontal-bar-counts>

    </section>
    <!-- Waarden -->
    <section class="section has-text-centered" v-if="selected.sector_or_functietype">
      <h3 class="title is-3">
        Waarden
      </h3>
      <h3 class="subtitle is-5 content">
        De test-afnemers kregen verspreid over 12 vragen 60 waarden voorgeschoteld. Bij elke vraag moesten ze de waarde 
        kiezen dewelke het best en het minst bij hen past. Op basis van deze antwoorden kunnen we bekijken welke waarden 
        populair zijn, en welke waarden bij elkaar horen. 
      </h3>    
      <div id="bubble-chart-latent-features-waarden" class="chartcontainer">
        <bubble-plot-waarden :selected="selected" 
                              :significant_sectors="significant_sectors" 
                              :significant_functietypes="significant_functietypes">
        </bubble-plot-waarden>
      </div>
      <!-- <div id="barchartwaarden" class="chartcontainer">
        <horizontal-bar-waarden :chartData="bar_chart_score_waarden_data" :options="bar_chart_score_waarden_options" :height='600' v-if="bar_chart_score_waarden_data"></horizontal-bar-waarden>
      </div>             -->
    </section>
    
    <!-- Competenties -->
    <section class="section has-text-centered" v-if="selected.sector_or_functietype">
      <h3 class="title is-3">
        Competenties
      </h3>
      <h3 class="subtitle is-5 content">
        De test-afnemers kregen verspreid over 11 vragen 55 competenties voorgeschoteld. Bij elke vraag moesten ze de competentie 
        kiezen dewelke het best en het minst bij hen past. Op basis van deze antwoorden kunnen we bekijken welke competenties 
        populair zijn, en welke competenties bij elkaar horen. 
      </h3>    
      <div id="bubble-chart-latent-features-competenties" class="chartcontainer">
        <bubble-plot-competenties :selected="selected" 
                              :significant_sectors="significant_sectors" 
                              :significant_functietypes="significant_functietypes">
        </bubble-plot-competenties>
      </div>
      <!-- <div id="barchartwaarden" class="chartcontainer">
        <horizontal-bar-waarden :chartData="bar_chart_score_waarden_data" :options="bar_chart_score_waarden_options" :height='600' v-if="bar_chart_score_waarden_data"></horizontal-bar-waarden>
      </div>             -->
    </section>    


  </div>
</template>

<script>
import unique_users from '@/data/unique_users.json'
import answers_waarden from '@/data/answers_waarden.json'

import HorizontalBarCounts from './HorizontalBarCounts'
import Histogram from './Histogram'

import BubblePlotWaarden from '@/components/BubblePlotWaarden.vue'
import BubblePlotCompetenties from '@/components/BubblePlotCompetenties.vue'
// import { GChart } from 'vue-google-charts'

export default {
  components:{
    'horizontal-bar-counts': HorizontalBarCounts,
    'histogram-age': Histogram,
    // 'horizontal-bar-waarden': HorizontalBarCounts,
    // 'latent-features-waarden': BubblePlot,
    // 'bubble-plot-waarden': GChart,
    'bubble-plot-waarden': BubblePlotWaarden,
    'bubble-plot-competenties': BubblePlotCompetenties,
  },
  data: function(){
    return{
      // read in data from jsons
      unique_users: unique_users,
      answers_waarden: answers_waarden,
      // selected values in select dropdowns
      selected:{
        sector:'Huidige sector',
        functietype:'Fuctietype',
        sector_or_functietype:null,
        value:'',
      },      
      // data for general plots about users
      unique_users_filtered : unique_users, 
      unique_users_filtered_male : unique_users,
      unique_users_filtered_female : unique_users,
      x_respondents:0,
      x_respondents_male:0,
      x_respondents_female:0,
      histogram_age_data:null,
      histogram_age_options:{},
      bar_chart_countsdegree_data:null,
      bar_chart_countsdegree_options:{},
      bar_chart_counts_sector_functietype_data:null,
      bar_chart_counts_sector_functietype_options:{},
      bar_chart_counts_sector_data:null,
      bar_chart_counts_sector_options:{},
      // significant functietypes
      significant_functietypes: [],
      significant_sectors: [],
      // data for count bar plot waarden
      answers_waarden_filtered : answers_waarden,
      bar_chart_score_waarden_data:null,
      bar_chart_score_waarden_options:{},
    }
  },
  methods:{
    selection(what_is_selected){
      // Set the values right
      if(what_is_selected == 'sector'){
        this.selected.sector_or_functietype = 'sector'
        this.selected.value = this.selected.sector
      }else if(what_is_selected == 'functietype'){
        this.selected.sector_or_functietype = 'functietype'
        this.selected.value = this.selected.functietype
      } 

      // Filter the unique users. Create arrays with unique users for the selected sector/type, male and female arrays
      const is_selected_value = (value) => {
        if(this.selected.sector_or_functietype=='sector'){
          return value['HUIDIGE SECTOR'] == this.selected.value
        }else if(this.selected.sector_or_functietype=='functietype'){
          return value['HUIDIG FUNCTIETYPE'] == this.selected.value
        }
      }
      this.unique_users_filtered = this.unique_users.filter(is_selected_value)
      this.x_respondents = this.unique_users_filtered.length
      this.unique_users_filtered_male = this.unique_users_filtered.filter(respondent => respondent.GESLACHT=='Man')
      this.x_respondents_male = this.unique_users_filtered_male.length
      this.unique_users_filtered_female = this.unique_users_filtered.filter(respondent => respondent.GESLACHT=='Vrouw')
      this.x_respondents_female = this.unique_users_filtered_female.length

      // Prepare the data for a histogram
      var temp_age_bins = [20, 25, 30, 35, 40, 45, 50, 55, 60, 65, 70]
      var temp_age_data = {'male':{},'female':{}}
      for(var i=0; i<this.unique_users_filtered_male.length; i++){
        // Get the current age, round of to nearest 5 years
        var temp_age = this.unique_users_filtered_male[i]['LEEFTIJD']
        
        var temp_age_rounded = Math.round(temp_age*2/10)/2*10
        if((temp_age_rounded < 20) | (temp_age_rounded > 70)) continue
        temp_age_data['male'][temp_age_rounded] = temp_age_data['male'][temp_age_rounded] ? temp_age_data['male'][temp_age_rounded] + 1 : 1
      }
      for(i=0; i<this.unique_users_filtered_female.length; i++){
        // Get the current age, round of to nearest 5 years
        temp_age = this.unique_users_filtered_female[i]['LEEFTIJD']
        temp_age_rounded = Math.round(temp_age*2/10)/2*10
        if((temp_age_rounded < 20) | (temp_age_rounded > 70)) continue
        temp_age_data['female'][temp_age_rounded] = temp_age_data['female'][temp_age_rounded] ? temp_age_data['male'][temp_age_rounded] + 1 : 1
      } 
      this.histogram_age_data = {
        'labels': temp_age_bins,
        'datasets': [{label:'mannen',
                      data: Object.values(temp_age_data['male']),
                      backgroundColor: '#8da0cb',
                    },
                    {label:'vrouwen',
                      data: Object.values(temp_age_data['female']),
                      backgroundColor:'#fc8d62',
                    }]
      }  
      this.histogram_age_options = {
        tooltips: {
          enabled:false,
        },        
        responsive:true,
        maintainAspectRatio: false,
        legend:{
          position:'bottom',
        },
        title:{
          display:true,
          text: 'Leeftijdsverdeling van de deelnemers',
          fontSize: 15,
        }
      }            
      
      // Prepare data for barplot education degree. Create the empty datastructure beforehand, so we don't have to sort
      var temp_counts_degree = {
        'universitair onderwijs':{'male':0, 'female':0},
        'hoger niet-universitair onderwijs':{'male':0, 'female':0},
        'algemeen secundair, volledig afgewerkt':{'male':0, 'female':0},
        'technisch of beroeps secundair, volledig afgewerkt':{'male':0, 'female':0},
        'algemeen secundair, niet volledig afgewerkt':{'male':0, 'female':0},
        'technisch of beroeps secundair, niet volledig afgewerkt':{'male':0, 'female':0},
        'lager onderwijs':{'male':0, 'female':0},
      }
      for (i = 0; i < this.unique_users_filtered_male.length; i++) {
        var temp_degree = this.unique_users_filtered_male[i]['OPLEIDING'];
        temp_counts_degree[temp_degree]['male'] = temp_counts_degree[temp_degree]['male'] + 1;
      }
      for (i = 0; i < this.unique_users_filtered_female.length; i++) {
        temp_degree = this.unique_users_filtered_female[i]['OPLEIDING'];
        temp_counts_degree[temp_degree]['female'] = temp_counts_degree[temp_degree]['female']+ 1;
      }
      this.bar_chart_countsdegree_data = {
        'labels': Object.keys(temp_counts_degree),
        'datasets': [{label:'mannen',
                      data: Object.values(temp_counts_degree).map(value => value['male']),
                      backgroundColor: '#8da0cb',
                    },
                    {label:'vrouwen',
                      data: Object.values(temp_counts_degree).map(value => value['female']),
                      backgroundColor:'#fc8d62',
                    }]
      }
      this.bar_chart_countsdegree_options = {
        tooltips: {
          enabled:false,
        },        
        responsive:true,
        maintainAspectRatio: false,
        legend:{
          position:'bottom',
        },
        title:{
          display:true,
          text: '# test afnemers per opleidingsniveau',
          fontSize: 15,
        }
      }
      
      // Prepare data for the barplot of the huidige sector. 
      // As I want to sort the counts to make the barplot look nicer, I store the counts in an array
      var temp_sector_counts = {}
      for (i = 0; i < this.unique_users_filtered.length; i++) {
        var temp_sector = this.unique_users_filtered[i]['HUIDIGE SECTOR'];
        temp_sector_counts[temp_sector] = temp_sector_counts[temp_sector] ? temp_sector_counts[temp_sector] + 1 : 1;
      }      
      temp_sector_counts = Object.entries(temp_sector_counts) // to array
      function compare_counts(a, b){ return  b[1] - a[1] } // sort function
      temp_sector_counts.sort(compare_counts) // sort
      this.bar_chart_counts_sector_data = { // convert to chart data
        'labels': temp_sector_counts.map(entry => entry[0]),
        'datasets':[{
          label:'Geen label',
          data: temp_sector_counts.map(entry => entry[1]),
          backgroundColor: '#8da0cb',
        }]
      }
      this.bar_chart_counts_sector_options = {
        tooltips: {
          enabled:false,
        },        
        responsive:true,
        maintainAspectRatio: false,
        legend:{
          display:false
        },
        title:{
          display:true,
          text: '# test afnemers per sector',
          fontSize: 15,
        }
      }

      // Prepare data for the barplot of the functietype. 
      // As I want to sort the counts to make the barplot look nicer, I store the counts in an array
      var temp_functietype_counts = {}
      for (i = 0; i < this.unique_users_filtered.length; i++) {
        var temp_functietype = this.unique_users_filtered[i]['HUIDIG FUNCTIETYPE'];
        temp_functietype_counts[temp_functietype] = temp_functietype_counts[temp_functietype] ? temp_functietype_counts[temp_functietype] + 1 : 1;
      }      
      temp_functietype_counts = Object.entries(temp_functietype_counts) // to array
      temp_functietype_counts.sort(compare_counts) // sort
      this.bar_chart_counts_functietype_data = { // convert to chart data
        'labels': temp_functietype_counts.map(entry => entry[0]),
        'datasets':[{
          label:'Geen label',
          data: temp_functietype_counts.map(entry => entry[1]),
          backgroundColor: '#8da0cb',
        }]
      }
      this.bar_chart_counts_functietype_options = {
        tooltips: {
          enabled:false,
        },        
        responsive:true,
        maintainAspectRatio: false,
        legend:{
          display:false
        },
        title:{
          display:true,
          text: '# test afnemers per functietype',
          fontSize: 15,
        }
      } 

      // Derive significant functietypes in case a sector is selected
      // A sector of functie type is significant if at least 5% of respondents clicked on it
      // With a maximum of 5?
      if(this.selected.sector_or_functietype == 'sector'){
        this.significant_functietypes = temp_functietype_counts.filter(entry => entry[1]/this.x_respondents > 0.075)
        this.significant_functietypes = this.significant_functietypes.map(entry => entry[0])
      }else if(this.selected.sector_or_functietype == 'functietype'){
        this.significant_sectors = temp_sector_counts.filter(entry => entry[1]/this.x_respondents > 0.075)
        this.significant_sectors = this.significant_sectors.map(entry => entry[0])
      }
      
      // Prepare data for the barplot of the waarden
      // Filter the answers of the waarden based on the users selected
      var temp_filtered_user_ids = this.unique_users_filtered.map(x => x['USER'])
      this.answers_waarden_filtered = this.answers_waarden.filter(x => temp_filtered_user_ids.includes(x['USER']))
      var temp_waarden_score = {}
      for (i = 0; i < this.answers_waarden_filtered.length; i++) {
              var temp_waarde = this.answers_waarden_filtered[i]['MINSTE'];
              temp_waarden_score[temp_waarde] = temp_waarden_score[temp_waarde] ? temp_waarden_score[temp_waarde] - 1 : -1;
              temp_waarde = this.answers_waarden_filtered[i]['BESTE'];
              temp_waarden_score[temp_waarde] = temp_waarden_score[temp_waarde] ? temp_waarden_score[temp_waarde] + 1 : 1;
      } 
      temp_waarden_score = Object.entries(temp_waarden_score) // to array
      function compare_score(a, b){ return  b[1] - a[1] } // sort function
      temp_waarden_score.sort(compare_score) // sort        
      this.bar_chart_score_waarden_data = { // convert to chart data
        'labels': temp_waarden_score.map(entry => entry[0]),
        'datasets':[{
          label:'Geen label',
          data: temp_waarden_score.map(entry => entry[1]),
          backgroundColor: '#8da0cb',
        }]
      }
      this.bar_chart_score_waarden_options = {
        tooltips: {
          enabled:false,
        },
        responsive:true,
        maintainAspectRatio: false,
        legend:{
          display:false
        },
        title:{
          display:true,
          text: '# score per waarde',
          fontSize: 15,
        }
      }      
    }
  }
}
</script>

